.searchBox {
  height: 32px;
  margin: 15px;
}
.noResults {
  padding: 0.785rem 1.142rem;
  color: rgba(0, 0, 0, 0.4);
}
.listWrapper {
  max-height: 200px;
  overflow-y: auto;
}
.listItem {
  outline: none;
  border: none;
  background-color: transparent;
  width: 100%;
  text-align: left;
  cursor: pointer;
  padding: 0.785rem 1.142rem;
}
.listItem:hover {
  background: rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.95);
}
