.panelGrid {
  margin: 0 !important;
  border-bottom: 1px solid #e2e2e3;
  height: 49px;
}
.searchTabsRow {
  padding: 0 !important;
  position: absolute !important;
}
.searchTabsColumn {
  max-width: calc(100% - 334px);
  flex: 1;
  width: unset !important;
  padding-left: 0 !important;
  padding-right: 15px !important;
}
.searchTabsWrapper {
  display: flex;
  height: 100%;
}
.searchButton:disabled {
  border: 1px solid rgba(34, 36, 38, 0.35) !important;
}
.searchMoreTabsWrapper {
  border-right: 1px solid #e2e2e3;
  min-width: 90px;
  max-width: 240px;
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center;
  user-select: none;
}
.searchTabWrapper {
  border-right: 1px solid #e2e2e3;
  min-width: 90px;
  max-width: 240px;
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  cursor: pointer;
  padding: 10px 10px 5px 20px;
}
.searchTabWrapper:hover {
  background-color: #f8f9f9;
}
.searchMoreTabsWrapper.hidden {
  display: none;
}
.searchMoreTabsDropdown {
  height: 100%;
  width: 100%;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}
.searchMoreTabsDropdown:hover {
  background-color: #f8f9f9;
}
.searchMoreTabsDropdown:focus {
  background-color: #f8f9f9;
}
.searchMoreTabsDropdownMenu {
  margin-top: 3px !important;
}
.searchMoreTabsDropdownItem:hover {
  background-color: unset !important;
}
.selected {
  background-color: #f8f9f9;
}
.searchTabWrapper.invisible {
  display: none;
}
.searchTabWrapper.hidden {
  padding: 10px;
}
.searchTabTextWrapper {
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  line-height: 16px;
}
.searchTabTextWrapper > div {
  overflow: hidden;
  text-overflow: ellipsis;
}
.searchTabTextWrapper > div:first-child {
  font-weight: 800;
}
.searchTabCloseButtonWrapper {
  margin-left: 10px;
}
.searchTabCloseButtonWrapper > button {
  cursor: pointer;
  border: none;
  background: none;
  padding: 0;
  outline: none;
}
.searchTabCloseButtonWrapper > button > svg {
  fill: #666;
}
.searchTabCloseButtonWrapper > button:hover > svg {
  fill: #333;
}
.panelSearchInputColumn {
  padding: 5px 15px !important;
  min-width: 334px;
  flex: 1;
  width: unset !important;
}
.searchInput {
  border-radius: 0 !important;
  text-overflow: ellipsis;
  border-left-color: transparent !important;
}
