.dropdown {
  position: relative;
  width: 100%;
}
.button {
  width: 100%;
  border: 1px solid rgba(34, 36, 38, 0.15);
  border-radius: 0.285rem;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  padding: 0.226em 2.1em 0.226em 0.357em;
  line-height: 1em;
  min-height: 2.714em;
  text-align: left;
}
.button:hover {
  border-color: rgba(34, 36, 38, 0.35);
}
.button > div {
  font-size: 14px;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  width: calc(100% - 5px);
  display: inline-block;
}
.button > div.text {
  margin: 0.452em 0 0.452em 0.642em;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.button > div.placeholder {
  color: rgba(191, 191, 191, 0.87);
}
.button > i {
  position: absolute;
  font-size: 0.857em !important;
  padding: 1em;
  top: 0;
  right: 0.3em;
  margin: 0 !important;
  width: auto !important;
  height: auto !important;
}
.button.disabled {
  cursor: unset;
  border: 1px solid rgba(34, 36, 38, 0.15);
}
.menuWrapper {
  position: relative;
}
.menu {
  position: absolute;
  top: 5px;
  background-color: #fff;
  right: 0;
  z-index: 1000;
  flex-direction: column;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
  border: 1px solid rgba(34, 36, 38, 0.15);
}
.item {
  position: relative;
  font-size: 14px;
  line-height: 1.4;
  cursor: pointer;
  padding: 0.8rem !important;
  border-bottom: 1px solid rgba(34, 36, 38, 0.15);
  user-select: none;
  outline: none;
  white-space: nowrap;
  display: flex;
  align-items: center;
}
.item > i {
  position: absolute;
  right: 10px;
  font-size: 0.857em !important;
}
.item:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
.item.open {
  background-color: rgba(0, 0, 0, 0.05);
}
.submenu {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
  border: 1px solid rgba(34, 36, 38, 0.15);
  z-index: 1100;
  position: fixed;
  color: unset;
}
.submenu > .pointer {
  margin: 0 0 1em;
  box-shadow: -1px -1px 0 0 rgba(34, 36, 38, 0.15);
  left: auto;
  right: -0.4em;
  transform: rotate(135deg);
  display: block;
  position: absolute;
  pointer-events: none;
  content: '';
  visibility: visible;
  width: 0.8em;
  height: 0.8em;
  background: #fff;
  z-index: 2;
}
