:global(.ui.isc.right.visible.sidebar) {
  overflow-y: unset !important;
}
:global(.ui.isc.dropdown > .menu > .item) {
  padding: 0 !important;
}
:global(.ui.isc.dropdown.error) {
  border-color: #9f3a38 !important;
}
:global(.ui.isc-selection.dropdown) {
  border-radius: 4px !important;
}
:global(.ui.isc-selection.dropdown:hover) {
  border-color: rgba(34, 36, 38, 0.35) !important;
  box-shadow: none !important;
}
:global(.ui.active.isc-selection.dropdown) {
  border-radius: 4px !important;
  box-shadow: none;
  border-color: rgba(34, 36, 38, 0.35) !important;
}
:global(.ui.isc-selection.dropdown > .menu) {
  border: 1px solid rgba(34, 36, 38, 0.15) !important;
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 4px;
}
:global(.ui.isc-selection.dropdown .menu .item) {
  border-bottom: 1px solid rgba(34, 36, 38, 0.15);
}
:global(.ui.isc-selection.dropdown .menu .active.item) {
  background: none;
  font-weight: 400;
}
:global(.ui.isc-selection.dropdown .menu .active.item:hover) {
  background: rgba(0, 0, 0, 0.05);
}
:global(.clndr-calendars-segment) {
  margin: 0 !important;
  border: none !important;
  box-shadow: none !important;
}
:global(.react-tabs) {
  outline: none;
}
:global(.react-tabs__tab-list) {
  margin: 0;
  padding: 0;
  border-bottom: 2px solid rgba(34, 36, 38, 0.15);
  box-shadow: none;
  font-size: 1rem;
  display: flex;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  background: #fff;
  font-weight: 400;
}
:global(.react-tabs__tab) {
  border-bottom: 2px solid transparent;
  border-radius: 0;
  align-self: flex-end;
  margin: 0 0 -2px;
  padding: 0.57em;
  transition: color 0.1s ease;
  list-style: none;
  cursor: pointer;
  outline: none;
  user-select: none;
  width: 100%;
  text-align: center;
}
:global(.react-tabs__tab--selected) {
  background-color: transparent;
  box-shadow: none;
  border-color: #1b1c1d;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.95);
}
:global(.react-tabs__tab-panel) {
  display: none;
  outline: red;
}
:global(.react-tabs__tab-panel--selected) {
  display: block;
}
