.sidebar {
  position: absolute;
  top: 49px;
  right: 0;
  width: 320px;
  height: calc(100% - 49px);
  background-color: #fff;
  overflow-y: auto;
  box-shadow: -3px 3px 6px #00000029;
  transition: ease-in-out 0.5s;
  z-index: 1000;
}
.sidebar.hidden {
  right: -330px;
}
.sidebar > .header {
  display: flex;
  flex-direction: row;
  padding: 5px 15px;
  justify-content: space-between;
}
.toggleButton {
  background: transparent none !important;
  border: 1px solid #dededf !important;
  padding: 10px !important;
  margin: 0 !important;
  height: 38px;
}
.toggleButton:hover {
  border-color: rgba(34, 36, 38, 0.35) !important;
}
.toggleButton:hover > svg {
  fill: #666;
}
.toggleButton.absolute {
  position: absolute;
  right: 20px;
  top: 54px;
}
.toggleButton > svg {
  height: 16px !important;
  width: 16px !important;
  fill: #767676;
}
.linkButton {
  border: none !important;
  color: #2185d0 !important;
  background: transparent !important;
  margin: 0 !important;
  padding: 0 !important;
  font-weight: 500 !important;
}
.linkButton:hover {
  color: #1678c2 !important;
}
.row,
.filterItem {
  padding: 0 15px;
  margin-top: 10px;
}
.filterItem > h5 {
  margin-bottom: 5px !important;
}
.filterItem > div {
  padding-left: 10px;
}
.dropdownFilterLabel {
  user-select: none;
  display: inline-block !important;
  vertical-align: top !important;
  white-space: normal;
  font-size: 1em !important;
  padding: 0.3571em 0.7857em !important;
  margin: 0.1428rem 0.2857rem 0.1428rem 0 !important;
  box-shadow: 0 0 0 1px rgba(34, 36, 38, 0.15) inset;
  word-break: break-all;
}
.mediaTypeFilterDropdown {
  display: flex !important;
  align-items: center;
  width: 107px !important;
  border-radius: 0.28rem 0 0 0.28rem;
  border: 1px solid rgba(34, 36, 38, 0.15);
  white-space: nowrap;
  margin-right: -1px;
  padding: 6px 10px;
}
.mediaTypeFilterDropdown > i {
  position: absolute !important;
  top: 50%;
  right: 10px;
  transform: translate(0, -50%);
  margin: 0 !important;
}
.mediaTypeFilterDropdown:hover {
  border: 1px solid rgba(34, 36, 38, 0.35);
}
:global(.ui.dropdown.active) > .mediaTypeFilterDropdownMenu:global(.menu) {
  width: max-content !important;
  border: 1px solid rgba(34, 36, 38, 0.15) !important;
  margin-top: 3px !important;
}
:global(.ui.dropdown.active) > .mediaTypeFilterDropdownMenu:global(.menu) > :global(.item) {
  padding: 0 !important;
}
.mediaTypeFilterDropdownHeader {
  text-transform: none !important;
  font-size: 14px !important;
}
.mediaTypeFilterDropdownItem {
  padding: 1.14rem !important;
  padding-right: 5rem !important;
  width: 100%;
}
.dateFilterTimepickerWrapper {
  display: flex;
  background: #fff;
  padding-left: 12px;
  padding-right: 12px;
  align-items: center;
  padding-bottom: 14px;
  margin-top: -7px;
}
.dateFilterTimepickerWrapper > span {
  margin-left: 10px;
  margin-right: 10px;
}
.durationFilterWrapper {
  display: flex;
}
.durationFilterWrapper > div:first-child {
  margin-right: 20px;
}
