.grid {
  display: inline-block;
  border-spacing: 0;
  width: inherit;
}
.gridHeader {
  overflow: hidden;
}
.gridBody {
  overflow: auto;
}
.headerRow {
  font-weight: 800;
  color: #333 !important;
  height: 35px;
  user-select: none;
}
.row {
  color: #333 !important;
  height: 35px;
  user-select: none;
}
.gridList {
  overflow: unset !important;
}
.row.selected {
  background-color: #f2f2f2;
}
.columnHeader {
  margin: 0;
  border-bottom: 1px solid #dee2e6;
}
.columnHeader:hover .columnMenuTriggerButton {
  opacity: 1;
}
.columnHeader.dragging {
  background: lightgray;
  cursor: grabbing !important;
}
.columnDraggable {
  cursor: pointer !important;
  height: 100%;
  width: 100%;
  padding: 0 5px 15px 15px;
}
.columnDraggable.dragging {
  cursor: grabbing !important;
}
.columnDraggable.notDragging {
  transform: translate(0, 0);
}
.columnDraggable.dropping {
  transition-duration: 0.001s;
}
.columnWrapper {
  display: flex;
  height: 100%;
  float: left;
  width: calc(100% - 29px);
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.columnText {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.columnSortArrow {
  margin-left: 5px;
}
.columnSortArrowIcon {
  height: 8px !important;
  width: 8px !important;
}
.columnMenuTriggerButtonWrapper {
  display: inline-block;
  float: right;
  margin-left: 5px;
}
.columnMenuTriggerButton {
  padding: 0 !important;
  background: transparent !important;
  opacity: 0;
  color: #b7b7b7 !important;
}
.columnMenuTriggerButton:hover {
  color: inherit !important;
}
.columnMenuWrapper {
  padding: 0 !important;
}
.columnMenuWrapper:before {
  content: none !important;
}
.columnMenu {
  width: 250px;
  padding: 0 !important;
}
.columnMenuCheckbox {
  display: block !important;
  padding: 7px 15px !important;
  cursor: pointer;
}
.columnMenuButton {
  outline: none;
  border: none;
  background-color: transparent;
  width: 100%;
  text-align: left;
  cursor: pointer;
  padding: 0.785rem 1.142rem;
}
.columnMenuButton:hover {
  background: rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.95);
}
.columnResizer {
  display: inline-block;
  width: 8px;
  height: 100%;
  position: absolute;
  right: -4px;
  top: 0;
  transform: translateX(50%);
  z-index: 1;
  touch-action: none;
  float: right;
}
.columnResizer:after {
  content: '';
  display: block;
  width: 1px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 10px;
  background: #dee2e6;
}
.cell {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
  padding: 15px 5px 15px 10px;
  border-bottom: 1px solid #dee2e6;
}
.cellContextMenuWrapper {
  margin-top: -20px !important;
  padding: 0 !important;
}
.cellContextMenuWrapper:before {
  content: none !important;
}
.cellContextMenuButton {
  margin: 0 !important;
  padding: 10px !important;
  background: transparent !important;
  color: #000000de !important;
  font-weight: 100 !important;
  min-width: 160px;
  text-align: left !important;
  line-height: 1em !important;
  user-select: none;
}
.cellContextMenuButton:hover {
  background-color: #f2f2f2 !important;
}
.cellContextMenuTooltip {
  margin: 0 !important;
  padding: 10px !important;
  background: transparent !important;
  color: #000000de !important;
  font-weight: 100 !important;
  min-width: 160px;
  text-align: left !important;
  line-height: 1em !important;
  user-select: none;
}
