.panelWrapper {
  padding: 5px 0 0 20px;
}
.panelWrapper.general {
  padding-top: 50px;
}
.searchInput {
  width: calc(100% - 70px);
}
.searchInput > button {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 45px;
  cursor: pointer;
  border: none;
  background: none;
  padding: 0;
  outline: none;
}
.searchInput > button > svg {
  fill: #666;
}
.searchInput > button:hover > svg {
  fill: #333;
}
.actionsPanel {
  margin-top: 20px;
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.actionsDropdown {
  border-radius: 0.28rem 0 0 0.28rem;
  border: 1px solid rgba(34, 36, 38, 0.15);
  white-space: nowrap;
  margin-right: 20px;
}
.gridWrapper {
  margin-top: 10px;
  width: 100%;
  position: absolute;
  right: 0;
  left: 0;
  padding: 0 5px 0 25px;
}
